				    url: '../out/out',
				});
			} else if(page === 1) {
				uni.navigateTo({
				    url: '../purchase/purchase',
				});
			} else if(page === 2) {
				uni.navigateTo({
				    url: '../sell/sell',
				});
			} else if(page === 3) {
				uni.navigateTo({
				    url: '../exchange/exchange',
				});
			}
		},
