		//编辑草稿
		editOrder(iden) {
			let diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "PR") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "SO") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "ST") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//关闭订单
		closeOrder(iden) {
			this.showHide = true
			this.closeIden = iden
		},
		clear1() {
			this.outFilterText = ''
		},
		clear2() {
			this.purchaseFilterText = ''
		},
		clear3() {
			this.sellFilterText = ''
		},
		clear4() {
			this.exchangeFilterText = ''
		},
		//隐藏输入框
		modelHide(){
			this.showHide=false
		},
		// 输入框确定按钮
		confirm(){
			let _this = this
			let myinfo = uni.getStorageSync('user_info')
			uni.showModal({
			    title: '提示',
			    content: '确认关闭请购单：'+_this.closeIden+" ?",
			    success: function (Res) {
			        if (Res.confirm) {
						let msg = {}
						msg.pr_iden = _this.closeIden
						msg.user_now_iden = myinfo.data.user.username
						msg.pr_closerReason = _this.closeReason
						_this.$http.post('/purchaseRequest/prClose', msg).then(([err,res]) => {
							//修改列表中该单据信息
							if(res.data.signal === 0) {
								for(let i =0; i<_this.purchaseList.length; i++) {
									if(_this.purchaseList[i].pr_iden === _this.closeIden){
										_this.purchaseList[i].pr_status = '2'
										break
									}
								}
							}
							uni.showToast({
								icon: 'none',
								position: 'bottom',
								title: res.data.message
							});
						})
						//清空缓存
						_this.closeIden = ''
						_this.closeReason = ''
			        } else if (Res.cancel) {
			            _this.closeIden = ''
			            _this.closeReason = ''
			        }
			    }
			});
			this.showHide=false
		},
		//判断权限
		judgePower() {
			let myinfo = uni.getStorageSync('user_info')
			let power = [0,0,0,0]
			for(let i=0; i<myinfo.data.roles.length; i++){
				let po = myinfo.data.roles[i][1]
				power[0] = power[0]<po[6]? po[6]:power[0]
				power[1] = power[1]<po[1]? po[1]:power[0]
				power[2] = power[2]<po[0]? po[0]:power[0]
				power[3] = power[3]<po[9]? po[9]:power[0]
			}
			
			if(power[0] === 2) {
				for(let i=0; i<myinfo.data.roles.length; i++){
					let po = myinfo.data.roles[i][1]
					if(po[6] === 1){
						power[0] = 3
					}
				}
			}
			if(power[1] === 2) {
				for(let i=0; i<myinfo.data.roles.length; i++){
					let po = myinfo.data.roles[i][1]
					if(po[1] === 1){
						power[1] = 3
					}
				}
			}
			if(power[2] === 2) {
				for(let i=0; i<myinfo.data.roles.length; i++){
					let po = myinfo.data.roles[i][1]
					if(po[0] === 1){
						power[2] = 3
					}
				}
			}
			if(power[3] === 2) {
				for(let i=0; i<myinfo.data.roles.length; i++){
					let po = myinfo.data.roles[i][1]
					if(po[9] === 1){
						power[3] = 3
					}
				}
			}
			uni.setStorageSync('power',power)
		}
	},
	
	onLoad: function() {   
		//登录检查，需要重写一下
		// loginMsg = this.checkLogin('../pages/main/main', 'switchTab');
		// if(!loginMsg){
		// 	return;
		// }
		
		//填入权限
		this.judgePower()
		let _this = this
		let myinfo = uni.getStorageSync('user_info')
		let power = uni.getStorageSync('power')
		//判断界面权限
		if(power[0] !== 0){
			this.items[0] = '出库'
		}
		if(power[1] !== 0){
			this.items[1] = '请购'
		}
		if(power[2] !== 0){
			this.items[2] = '销售'
		}
		if(power[3] !== 0){
			this.items[3] = '转库'
		}
		this.$forceUpdate()
		
		
		//TODO 主页加载时默认搜索出库单
		// let outMes = {}
		// outMes.area_name = myinfo.data.user.area_name
